import { useState } from "react";
import styled from "styled-components"
import esnlogo from "./image3.png"

const Wrapper = styled.div`
  	display: flex;
    flex: 1;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(219, 240, 204));
    justify-content: space-around;
    align-items: center;
    @media (max-width: 726px) {
      flex-direction: column;
    }
`;

const Image = styled.img`
  object-fit: contain;
    height: 20vw;
    width: 40vw;
`;

const LoginForm = styled.form`
    display: flex;
    padding: 1em;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const Title = styled.h1`
    font-size: 6rem;
    padding: 0.5rem;
`;

const Label = styled.label`
    font-size: 4rem;
    padding: 0.5rem;
`;

const Input = styled.input`
    font-size: 2rem;
    border-radius: 1rem;
    padding: 0.5rem;
    border: none;
    border-bottom: 0.2rem solid rgb(194, 178, 176);
    outline: none;
    background-color: rgb(224, 214, 209);
    :focus {
      border-bottom: 0.2rem solid black
    }
`;

const Button = styled.input`
    font-size: 2.5rem;
    border-radius: 1rem;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(214, 210, 208));
    margin: 1rem;
    padding: 0.5rem;
`;

const ErrorMessage = styled.p`
    font-size: 2.5rem;
    color: red;
`;

const Login = () => {
  const [loginfo, setLoginfo] = useState({ username: "", password: "" });
  const [errorinfo, setErrorinfo] = useState();

  function sendLoginfo(e) {
    e.preventDefault()
    fetch("/auth/login", {
      method: "POST",
      mode: "cors",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(loginfo),
    })
      .then((res) => res.json())
      .then((res) => {
        if (res === loginfo.username) {
          localStorage.setItem("user", res);
          window.location.href = "/";
        } else {
          setLoginfo({ username: "", password: "" });
          setErrorinfo(res.message);
        }
      })
      .catch((error) => console.log(error));
  }

  function handleInput(e) {
    const { name, value } = e.target;
    setLoginfo((prevInfo) => {
        return { ...prevInfo, [name]: value };
    });
  }

  return (
    <Wrapper>
      <Image src={esnlogo}></Image>
    <LoginForm onSubmit={sendLoginfo}>
      <Title>Prijava</Title>
      <Label for="username">Korisničko ime:</Label>
      <Input
        id="username"
        type="text"
        name="username"
        onChange={handleInput}
        value={loginfo.username}
      />
      <Label for="password">Šifra:</Label>
      <Input
        id="password"
        type="password"
        name="password"
        onChange={handleInput}
        value={loginfo.password}
      />
      <Button value="Prijava" type="submit" />
      <ErrorMessage>{errorinfo ? `${errorinfo}*` : ""}</ErrorMessage>
    </LoginForm>
    </Wrapper>
  );
};

export default Login;
