import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

const Wrapper = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Message = styled.h1`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5em;
  color: black;
  padding: 2em;
`;

const GoHome = styled.h2`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5em;
  color: black;
  padding: 2em;
`;

const NotFound = () => (
  <Wrapper>
    <Message>404 - Nije pronađeno!</Message>
    <GoHome>
      <Link to="/">Idi na početnu!</Link>
    </GoHome>
  </Wrapper>
);

export default NotFound;
