import { Link } from "react-router-dom"
import styled from "styled-components"

const Navstyle = styled.nav`
    display: flex;
    justify-content: flex-end;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(213, 247, 173));
`;

const Navbar = () => {

    function Logout() {
        fetch("/logout")
        localStorage.clear()
        window.location.href="/"
    }

    return (
        <Navstyle>
            <Link>Users</Link>
            <Link>Events</Link>
            <div onClick={Logout}>Logout</div>
        </Navstyle>
    )
}

export default Navbar;
